import { useState } from 'react';
// MUI
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
import SvgColor from 'src/components/svg-color';
// route
import { paths } from 'src/routes/paths';
// config global
import { useLocales } from 'src/locales';

// ----------------------------------------------------------------------

export default function WrittingView() {
  const [fullscreen, setFullscreen] = useState(false);

  const { t } = useLocales();
  return (
    <>
      <CustomBreadcrumbs
        heading={t('Soạn văn bản điện tử')}
        links={[
          { name: t('Danh mục') },
          {
            name: t('Giáo án'),
            href: paths.home.lesson_plans.root,
          },
          { name: t('Soạn văn bản') },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Card
        sx={{
          height: '100vh',
          mx: 3,
          ...(fullscreen && {
            position: 'fixed',
            inset: 0,
            width: '100vw',
            mx: 0,
            borderRadius: 0,
            zIndex: 999999999999,
          }),
        }}
      >
        <Button
          variant="contained"
          color="inherit"
          onClick={() => setFullscreen(!fullscreen)}
          sx={{
            position: 'absolute',
            right: 12,
            top: 12,
          }}
        >
          <SvgColor
            src={`/assets/icons/setting/${
              fullscreen ? 'ic_exit_full_screen' : 'ic_full_screen'
            }.svg`}
            sx={{ width: 16, height: 16, mr: 1 }}
          />

          {fullscreen ? t('Thoát toàn màn hình') : t('Toàn màn hình')}
        </Button>

        <iframe
          title="google slide"
          src="https://docs.google.com/document/d/1G3oe54cpIcCXLoiAJNdKWPNn3HuyMPD69oMPYD1NWFo/edit?usp=sharing"
          style={{
            width: '100%',
            height: '100%',
            border: 'none',
          }}
        />
      </Card>
    </>
  );
}
